import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/HomeViews'
Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'home',
    component: Home
  },
  {
    path: '/detail',
    name: 'detail',
    component: () => {
      return import('@/views/DetailViews')
    }
  },
  {
    path: '/willDetail',
    name: 'willDetail',
    component: () => {
      return import('@/views/willDetail')
    }
  },
  {
    path: '/Yyuan',
    name: 'Yyuan',
    component: () => {
      return import('@/components/Yyuan.vue')
    }
  },
  {
    path: '/xq',
    name: 'xq',
    component: () => {
      return import('../components/Yyuan/Xqing.vue')
    }
  },
  {
    path: '/search',
    name: 'search',
    component: () => {
      return import('../components/Yyuan/Search.vue')
    }
  },
  {
    path: '/ZiXun',
    name: 'ZiXun',
    component: () => {
      return import('@/views/ZiXun.vue')
    }
  },
  {
    path: '/yingpian',
    name: 'yingpian',
    component: () => {
      return import('@/views/YingPian.vue')
    }
  },
  {
    //路径
    path: "/me",
    // 命名
    name: "me",
    // 关联一个组件
    component: () => {
      return import('@/components/Me.vue')
    },
  },
  {
    //路径
    path: "/login",
    // 命名
    name: "login",
    // 关联一个组件
    component: () => {
      return import("@/views/Login.vue");
    },
  },
  {
    path:'/city',
    name:'city',
    component:()=>{
      return  import('@/components/城市选择.vue')
    },

  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
