<template>
    <div class="topbar">
       <div class="nav-sub">
     <van-nav-bar title="电影" :class="fixed == true ? 'fixed' : ''">
        <template #left>
        北京
        <span class="iconfont icon-xiala"></span>
        </template>
    </van-nav-bar> 
    </div>
   </div>
  </template>
<script>
import '../static/iconfont.css'
import Vue from 'vue';
import { NavBar } from 'vant';
Vue.use(NavBar);
export default {
    data () {
      return { fixed: false }
    },
    mounted () {
      window.addEventListener('scroll', this.fixedActiveBtn)
    },
    methods: {
      fixedActiveBtn () {
        var scrollTop =
          window.pageYOffset ||
          document.documentElement.scrollTop ||
          document.body.scrollTop
        scrollTop >= 120 ? (this.fixed = true) : (this.fixed = false)
      }
    }
}
</script>

<style scoped>
.topbar{
    background: #fff;
}
  .nav-sub {
     /* position: relative; */
    /* height: 90px; */
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
    /* position: sticky; */
  }
  .fixed {
    position: fixed;
    z-index: 21;
    height: 60px;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    border-bottom: 1px solid #fff;
    /* background-image: -webkit-linear-gradient(#fff, #f1f1f1);
    background-image: linear-gradient(#fff, #f1f1f1); */
  }
</style>