<template>

<div style="margin-bottom: 50PX;">
  <div class="tab">
    <div class="topscoll-no" id="tpsc" v-v-scroll>
      <span class="iconfont icon-xiala" @click="jump()" style="float: left;">北京</span>
      <span style="text-align: center;">
        电影
      </span>
    </div>
    <van-tabs v-model="active" animated>
      <van-tab title="正在热映">
        <div v-for="(list, index) in lists" :key="index">
          <router-link to="/yingpian">
            <div class="item">
              <img :src="list.poster" alt="" />
              <div class="main-content">
                <div class="intro">
                  <p class="name">
                    <span class="word">{{ list.name }}</span>
                    <span class="w2">{{ list.filmType.name }}</span>
                  </p>
                  <p class="grade">
                    <span class="g1">{{ value }}</span>
                    <span class="g2">{{ list.grade }}</span>
                  </p>
                  <p class="label">
                    <span class="l2">主演：</span>
                    <span
                      class="l1"
                      v-for="(actor, i) in list.actors"
                      :key="i"
                      >{{ actor.name }}</span
                    >
                  </p>
                  <p class="space">
                    <span class="time"
                      >{{ list.nation }} | {{ list.runtime }}</span
                    >
                  </p>
                </div>
                <div class="prop">购票</div>
              </div>
            </div>
          </router-link>
        </div>
      </van-tab>
      <van-tab title="即将上映">
        <div v-for="(content, i) in contents" :key="i">
          <div class="item" @click="will">
            <img :src="content.poster" alt="" />
            <div class="main-content">
              <div class="intro">
                <p class="name">
                  <span class="word">{{ content.name }}</span>
                  <span class="w2">{{ content.filmType.name }}</span>
                </p>
                <p class="label">
                  <span class="l2">主演：</span>
                  <span
                    class="l1"
                    v-for="(actor, i) in content.actors"
                    :key="i"
                    >{{ actor.name }}</span
                  >
                </p>
                <p class="label">
                  <span class="l2">上映日期：</span>
                  <span class="l1">2023年1月10日</span>
                </p>
              </div>
              <div class="prop" v-show="content.isPresale">预购</div>
            </div>
          </div>
        </div>
      </van-tab>
    </van-tabs>
    <div class="choice">选座购票</div>
    <!-- <div class="box"></div> -->
    

    </div>
  </div>
</template>
 <script>
import "../static/iconfont.css";
import Vue from "vue";
import { Tab, Tabs } from "vant";
import { Loading } from "vant";
Vue.use(Loading);
Vue.use(Tab);
Vue.use(Tabs);
export default {
  directives: {
    "v-scroll": {
      inserted(el, binds) {
        window.onscroll = function () {
          var t = document.documentElement.scrollTop || document.body.scrollTop;
          if (t > 100) {
            el.className = "topscolls";
          } else {
            tpsc.className = "topscoll-no";
          }
        };
      },
    },
  },
  data() {
    return {
      active: 2,
      lists: [],
      contents: [],
      value: "观众评分",
    };
  },
  methods: {
    will() {
      this.$router.push({ name: "willDetail" });
    },
    jump(){
      this.$router.push({name:'city'})
    }
  },
  created() {
    this.axios
      .get(
        "https://m.maizuo.com/gateway?cityId=210300&pageNum=1&pageSize=10&type=2&k=9542636",
        {
          headers: {
            "X-Client-Info":
              '{"a":"3000","ch":"1002","v":"5.2.1","e":"167601477585096187035649","bc":"110100"}',
            "X-Host": " mall.film-ticket.film.list",
          },
        }
      )
      .then((res) => {
        // console.log(res.data.data.films);
        this.contents = [...res.data.data.films];
      })
      .catch((err) => {
        console.log(err);
      });
    this.axios
      .get(
        "https://m.maizuo.com/gateway?cityId=110100&pageNum=1&pageSize=10&type=1&k=8598522",
        {
          headers: {
            "X-Client-Info":
              '{"a":"3000","ch":"1002","v":"5.2.1","e":"167601477585096187035649","bc":"110100"}',
            "X-Host": " mall.film-ticket.film.list",
          },
        }
      )
      .then((res) => {
        // console.log(res.data.data);
        this.lists = [...res.data.data.films];
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>
  
  <style scoped lang="scss">
.item {
  display: flex;
  padding: 15px;
  border-bottom: 1px solid #ededed;
  img {
    width: 66px;
    height: 93px;
  }
  .main-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .intro {
      width: 230px;
      padding: 0 10px;
      .name {
        margin-bottom: 2px;
        .word {
          max-width: calc(100% - 25px);
          color: #191a1b;
          font-size: 16px;
          height: 22px;
          line-height: 22px;
          margin-right: 5px;
          overflow: hidden;
        }
        .w2 {
          font-size: 9px;
          color: #fff;
          background-color: #d2d6dc;
          height: 14px;
          line-height: 14px;
          padding: 0 2px;
          border-radius: 2px;
        }
      }
      .grade {
        margin-bottom: 3px;
        .g1 {
          font-size: 13px;
          margin-top: 4px;
          color: #797d82;
        }
        .g2 {
          color: #ffb232;
          font-size: 14px;
        }
      }
      .label {
        margin-bottom: 3px;
        overflow: hidden; //超出的文本隐藏
        text-overflow: ellipsis; //溢出用省略号显示
        white-space: nowrap; // 默认不换行
        .l1 {
          display: inline-block;
          font-size: 13px;
          margin-top: 4px;
          color: #797d82;
          margin-right: 3px;
        }
        .l2 {
          font-size: 13px;
          color: #797d82;
        }
      }
      .space {
        margin-top: 3px;
        .time {
          font-size: 13px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 100%;
        }
      }
    }
    .prop {
      width: 50px;
      height: 25px;
      border: 1px solid #ff5f16;
      text-align: center;
      line-height: 22px;
      border-radius: 2px;
      font-size: 13px;
      color: #ff5f16;
    }
  }
}
.choice {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 49px;
  width: 100%;
  text-align: center;
  background-color: #ff5f16;
  color: #fff;
  font-size: 16px;
  line-height: 49px;
}
.box {
  height: 35px;
  background: #c1c1c1;
}
:deep(.van-tab--active) {
  color: #ff5f16;
}
:deep(.van-tabs__line) {
  width: 60px;
  height: 1px;
  color: #ff5f16;
}
.topscolls {
  position: fixed;
  width: 100%;
  height: 44px;
  background-color: #fff;
  text-align: center;
  line-height: 44px;
  z-index: 10;
  top: 0;
}

// 不滚动
.topscoll-no {
  display: none;
}
</style>